.App {
  background-color: #111;
}
.row{
    margin-left: 20px;
    color: white;
}
.row_poster {
    object-fit: contain;
    width: 300px;
    max-height: 200px;
    transition: -webkit-transform 250ms;
    transition: transform 250ms;
    transition: transform 250ms, -webkit-transform 250ms;
    margin-right: 10px;
}

.row_posters::-webkit-scrollbar {
    display: none;
}

.row_posters {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
  }

.row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_poster:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.row_posterLarge {
    max-height: 300px;
    margin: 0 5px;
    transition: -webkit-transform 250ms;
    transition: transform 250ms;
    transition: transform 250ms, -webkit-transform 250ms;
}

.row_posterLarge:hover {
    -webkit-transform: scale(1.08);
            transform: scale(1.08)
}
.banner {
    color: white;
    object-fit: contain;
    height: 448px;
}

.banner_contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}

.banner_button {
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    padding-bottom: 0.5rem;
}

.banner_button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}

.banner--fadeBotton {
    height: 7.4rem;
    margin-left: 0;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61),
        #111
    );
}
.nav {
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 20px;
    height: 30px;
    align-items: center;
    z-index: 1;
    transition: all 0.3s;
    transition-timing-function: ease-in;
}

.nav_logo {
    position: fixed;
    left: 30px;
    width: 80px;
    object-fit: contain;
}

.nav_avatar {
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}

.nav_black{
    background-color: black;
}
